<template>
    <div class="project">
        <div class="space_top">
            <div @click="reloadPage()">
                <img
                        src="../../assets/swt_img/logo_n.png"
                        alt=""
                        class="space_top_logo"
                />
            </div>
            <div class="space_top_right">
                <img
                        src="../../assets/swt_img/right_menu.png"
                        alt=""
                        class="space_top_right_img2"
                        @click="showMean()"
                />
            </div>
        </div>
        <div class="space_back">
            <div class="space_home_trade">
                <div>
                    <span class="project_title"></span>
                    <p class="project_detail">
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;(SWT),5000W,,SWT,SWT
                        SWT,NFT
                    </p>
                </div>
                <div class="project_mid">
                    <img
                            class="project_img3"
                            src="../../assets/swt_img/project_img3.png"
                            alt=""
                    />
                    <p class="project_content" @click="toToken">
                        : 0x51d510a4e9529AA4AfC00bd2D77D5A31925E8A58
                    </p>
                </div>

                <div class="project_white">
                    <div class="project_now">
                        <div class="project_now_left">
                            <div class="project_now_title"></div>
                            <div class="project_now_t">
                                <p></p>
                                <p></p>
                                <!-- <p>24H</p> -->
                            </div>
                            <div class="project_now_l1">
                                <p>SWT</p>
                                <p>$0.1000</p>
                                <!-- <p>+11.2%</p> -->
                            </div>
                            <div class="project_now_l2">
                                <p>ETH</p>
                                <p v-if="priceShow">${{ parseFloat(ETHModel.price).toFixed(4) || "0.0000" }}</p>
                                <!-- <p>$12.5</p> -->
                            </div>
                            <div class="project_now_l1">
                                <p>EOS</p>
                                <p v-if="priceShow">${{ parseFloat(EOSModel.price).toFixed(4) || "0.0000" }}</p>
                                <!-- <p>+11.2%</p> -->
                            </div>
                        </div>
                        <div class="project_now_right">
                            <img src="../../assets/swt_img/project_img1.png" alt=""/>
                        </div>
                    </div>
                </div>

                <div>
                    <span class="project_title"></span>
                    <div class="project_rule">
                        <div class="project_rule_left">
                            <p>1.,3%</p>
                            <p>2.</p>
                            <p>3.1000W</p>
                        </div>
                        <div class="project_rule_right">
                            <img
                                    @click="toPancakeswap"
                                    src="../../assets/swt_img/project_img2.png"
                                    alt=""
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <template v-if="meanShow">
            <div class="index_pop_content">
                <div class="index_pop_top">
                    <div class="index_pop_top_left">
                        <img src="../../assets/swt_img/logo_w.png" alt=""/>
                    </div>
                    <div>
                        <img
                                src="../../assets/swt_img/tea_close.png"
                                alt=""
                                class="space_mean_img"
                                @click="showMean()"
                        />
                    </div>
                </div>
                <div class="index_pop_main_tab" @click="goback()">
                    <div class="index_pop_middle">
                        <a href="javascript:;">
                            <div class="index_pop_middle_left">
                                <span></span>
                            </div>
                            <div class="index_pop_middle_right"></div>
                            <div class="index_clear"></div>
                        </a>
                    </div>
                </div>
                <div class="index_pop_main_tab" @click="meanShow = false">
                    <div class="index_pop_middle">
                        <a href="javascript:;">
                            <div class="index_pop_middle_left">
                                <span></span>
                            </div>
                            <div class="index_pop_middle_right"></div>
                            <div class="index_clear"></div>
                        </a>
                    </div>
                </div>
                <div class="index_pop_main_tab" @click="goback()">
                    <div class="index_pop_middle">
                        <a href="javascript:;">
                            <div class="index_pop_middle_left">
                                <span></span>
                            </div>
                            <div class="index_pop_middle_right"></div>
                            <div class="index_clear"></div>
                        </a>
                    </div>
                </div>
                <div class="index_pop_main_tab" @click="goback()">
                    <div class="index_pop_middle">
                        <a href="javascript:;">
                            <div class="index_pop_middle_left">
                                <span></span>
                            </div>
                            <div class="index_pop_middle_right"></div>
                            <div class="index_clear"></div>
                        </a>
                    </div>
                </div>
                <div class="index_pop_main_tab" @click="goback()">
                    <div class="index_pop_middle">
                        <a href="javascript:;">
                            <div class="index_pop_middle_left">
                                <span>NFT</span>
                            </div>
                            <div class="index_pop_middle_right"></div>
                            <div class="index_clear"></div>
                        </a>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>
<script>
    import {WOW} from "wowjs";
    import Swiper from "swiper";
    import {
        apiRegister,
        apiGetArtList,
        apiRecommendList,
        apiBannerList,
        apiGetConfig,
    } from "@/request/api";

    let line;
    let provider, signer, defaultAccount, networkVersion;
    let conContract;
    let timeStart;
    export default {
        data() {
            return {
                meanShow: false,
                connect_show: false,
                langShow: false,
                bannerList: [],
                ETHModel: {},
                EOSModel: {},
                priceShow:false,
            };
        },
        created: function () {
            // this.getBannerList();
            this.getETHprice();
            this.getEOSprice();
        },
        beforeDestroy() {
            if (timeStart) {
                // 
                clearInterval(timeStart); //
            }
        },
        async mounted() {
            var wow = new WOW({
                boxClass: "wow", // animated element css class (default is wow)
                animateClass: "animated", // animation css class (default is animated)
                offset: 0, // distance to the element when triggering the animation (default is 0)
                mobile: true, // trigger animations on mobile devices (default is true)
                live: true, // act on asynchronously loaded content (default is true)
                callback: function (box) {
                    // the callback is fired every time an animation is started
                    // the argument that is passed in is the DOM node being animated
                },
                scrollContainer: null, // optional scroll container selector, otherwise use window
            });
            wow.init();
            new Swiper(".banner-swiper", {
                autoplay: true, //
                loop: true,
                pagination: {
                    el: ".swiper-pagination",
                    clickable: true,
                },
                lazy: {
                    loadPrevNext: true,
                },
                observer: true,
                observeParents: true,
            });
            var _this = this;
            timeStart = setInterval(function () {
                _this.getETHprice();
                _this.getEOSprice();
            }, 10000);
        },
        methods: {
            toHome() {
                this.$router.push({
                    name: "home",
                    query: {
                        // p_address: this.p_address,
                    },
                });
            },
            goback() {
                this.$router.replace({
                    name: "home",
                    query: {},
                });
            },
            toNftTrade() {
                this.$router.push({
                    name: "project",
                    query: {
                        // p_address: this.p_address,
                    },
                });
            },
            reloadPage() {
                location.reload();
            },
            //
            showMean() {
                if (this.meanShow) {
                    this.meanShow = false;
                } else {
                    this.meanShow = true;
                }
            },
            getBannerList() {
                var _this = this;
                // 
                apiBannerList({}).then((res) => {
                    _this.bannerList = res.data;
                    console.log(res);
                });
            },
            getETHprice() {
                let _this = this;
                this.priceShow = false;
                $.get(
                    this.api.ethUrl,
                    {},
                    function (data) {
                        if (data.code == 200) {
                            _this.priceShow = true;
                            _this.ETHModel = data.data.markets[0];
                            // console.log(_this.ETHModel.price);
                        }
                    },
                    "JSON"
                );
            },
            getEOSprice() {
                let _this = this;
                this.priceShow = false;
                $.get(
                    this.api.eosUrl,
                    {},
                    function (data) {
                        if (data.code == 200) {
                            _this.priceShow = true;
                            _this.EOSModel = data.data.markets[0];
                            // console.log(_this.EOSModel.price);
                        }
                    },
                    "JSON"
                );
            },
            toToken() {
                window.open(
                    "https://bscscan.com/address/" + this.api.swtAddress,
                    "_self"
                );
            },
            toPancakeswap() {
                window.open(
                    "https://pancakeswap.finance/swap?outputCurrency=" +
                    this.api.swtAddress +
                    "&inputCurrency=0x55d398326f99059ff775485246999027b3197955&utm_source=tokenpocket",
                    "_self"
                );
            },
        },
    };
</script>


